import React from 'react';
import { Carousel, Container } from 'react-bootstrap';
import ck from "../assets/ck.jpg";
import orient  from "../assets/orient.jpg";
import diesel from "../assets/diesel.jpg";
import victorinox from "../assets/victorinox.jpg";
import parker from "../assets/parker.jpg";
import waterman from "../assets/waterman.jpg";

export default function CarouselBox() {
    return (
        <Container>
            <Carousel className='b-1'>
                <Carousel.Item>
                    <img className="b-block w-100" src={ ck } alt=" Calvin Klein  "/>
                    <Carousel.Caption className='hide-on-mobile'>
                        <h3 style={{color: "black"}}>Calvin Klein</h3>
                        <p style={{color: "black"}}>,         .</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img className="b-block w-100" src={ orient } alt=" Orient  " />
                    <Carousel.Caption className='hide-on-mobile'>
                        <h3>Orient</h3>
                        <p> ,  ,    .</p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img className="b-block w-100" src={ diesel } alt=" Diesel  " />
                    <Carousel.Caption>
                        <h3>Diesel</h3>
                        <p>     .  .</p>
                    </Carousel.Caption>
                    </Carousel.Item>
                <Carousel.Item>
                    <img className="b-block w-100" src={ victorinox } alt=" Victorinox  " />
                    <Carousel.Caption className='hide-on-mobile'>
                        <h3 style={{color: "black"}}>Victorinox</h3>
                        <p style={{color: "black"}}>    </p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img className="b-block w-100" src={ parker } alt=" Parker  " />
                    <Carousel.Caption className='hide-on-mobile'>
                        <h3>Parker</h3>
                        <p>   </p>
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img className="b-block w-100" src={ waterman } alt=" Waterman  " />
                    <Carousel.Caption>
                        <h3>Waterman</h3>
                        <p>,    .</p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </Container>
    )
}