import { Card, Col, Row } from 'react-bootstrap'
import Container from 'react-bootstrap/Container'
import { Helmet } from 'react-helmet'
import parker from "../assets/parker-logo.png"
import parkerpen from "../assets/parker1.jpg"
import parkerp from "../assets/parker2.jpg"
import go from "../assets/go.png";

const Parker = () => {
    return (
        <Container>
            <Helmet>
                <title>   Parker   - Aksessuary.KZ</title>
                <meta name="description" content="    Parker  "/>
                <meta name="keywords" content="  Parker  ,    Parker  ,    Parker  , 
                      Parker, Parker,  Parker  " />
            </Helmet>
            <Card>
                <img src={ parker } alt="   Parker  "  width='40%' style={{display: 'block', marginLeft: 'auto', marginRight: 'auto'}}/>
                <p style={{textAlign: 'justify', marginLeft: '20px', marginRight: '20px'}}>
                     1888  PARKER             ,     . 
                            ,    ,        
                               PARKER.
                </p>
                <p style={{textAlign: 'justify', marginLeft: '20px', marginRight: '20px'}}>
                     PARKER      125           .    
                    ,      ,  PARKER   .        
                          ,       PARKER     125 .      
                        .    PARKER     ,     .
                </p>
                <h4 style={{textAlign: 'center', color: 'red'}}>            !</h4>
                <Row style={{marginLeft: '10px', marginRight: '10px', marginBottom: '20px', marginTop: '10px'}}>
                    <Col md={8}>
                        <img src={ parkerpen } alt="  Parker  " width='94%'/>
                    </Col>
                    <Col md={4}>
                        <img src={ parkerp } alt="   Parker  " width='100%'/>
                    </Col>
                </Row>
                <a href='/shop'><img class='scalecat' src={ go } alt="   Parker  " style={{width: '20%', display: 'block', marginLeft: 'auto', marginRight: 'auto', marginBottom: '20px' }} /></a>
            </Card>
        </Container>
    )
}

export default Parker