import { Card, Col, Row } from 'react-bootstrap'
import Container from 'react-bootstrap/Container'
import { Helmet } from 'react-helmet'
import ak from "../assets/ak-logo.png"
import annek from "../assets/anneklein.jpg"
import anneklein from "../assets/anneklein2.jpg"
import go from "../assets/go.png";

const AK = () => {
    return (
        <Container>
            <Helmet>
                <title>  Anne Klein   - Aksessuary.KZ</title>
                <meta name="description" content="    Anne Klein  "/>
                <meta name="keywords" content="  Anne Klein  ,    Anne Klein  ,   Anne Klein  , 
                      Anne Klein, Anne Klein,  Anne Klein  " />
            </Helmet>
            <Card>
                <img src={ ak } alt="  Anne Klein  "  width='35%' style={{display: 'block', marginLeft: 'auto', marginRight: 'auto', marginTop: 15}}/>
                <p style={{textAlign: 'justify', marginLeft: '20px', marginRight: '20px'}}>
                            ,   ,  ,   ,    ,   . 
                             ,      !        
                    Anne Klein     .         ,  ,     ,   
                    ,      Swarovski.         
                </p>
                <p style={{textAlign: 'justify', marginLeft: '20px', marginRight: '20px'}}>
                      :      ,   ,        .   Anne Klein   
                      ,               ,      
                            -   ,           !
                </p>
                <p style={{textAlign: 'center', marginLeft: '20px', marginRight: '20px'}}>
                           ,    .        !
                </p>
                <h4 style={{textAlign: 'center', color: 'red'}}>            !</h4>
                <Row style={{marginLeft: '10px', marginRight: '10px', marginBottom: '20px', marginTop: '10px'}}>
                    <Col md={8}>
                        <img src={ annek } alt="  Anne Klein  " width='100%'/>
                    </Col>
                    <Col md={4}>
                        <img src={ anneklein } alt="   Anne Klein  " width='90%'/>
                    </Col>
                </Row>
                <a href='/shop?brand=2'><img class='scalecat' src={ go } alt="   Anne Kleinx  " style={{width: '20%', display: 'block', marginLeft: 'auto', marginRight: 'auto', marginBottom: '20px' }} /></a>
            </Card>
        </Container>
    )
}

export default AK