import { Table } from 'react-bootstrap'

const Order = (props) => {
    return (
        <>
            <ul>
                <li> : {props.data.prettyCreatedAt}</li>
                <li>
                     :
                    {props.data.status === 0 && <span></span>}
                    {props.data.status === 1 && <span> </span>}
                    {props.data.status === 2 && <span></span>}
                </li>
            </ul>
            <ul>
                <li>, : {props.data.name}</li>
                <li> : {props.data.email}</li>
                <li> : {props.data.phone}</li>
                <li> : {props.data.address}</li>
                <li>: {props.data.comment}</li>
            </ul>
            <Table bordered hover size="sm" className="mt-3">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th>-</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {props.data.items.map(item => 
                        <tr key={item.id}>
                            <td>{item.name}</td>
                            <td>{item.price}</td>
                            <td>{item.quantity}</td>
                            <td>{item.price * item.quantity}</td>
                        </tr>
                    )}
                    <tr>
                        <td colSpan={3}></td>
                        <td>{props.data.amount}</td>
                    </tr>
                </tbody>
            </Table>
        </>
    )
}

export default Order