import { Card, Col, Row } from 'react-bootstrap'
import Container from 'react-bootstrap/Container'
import { Helmet } from 'react-helmet'
import victorinox from "../assets/victorinox-logo.png"
import army from "../assets/victorinox.png"
import kitchen from "../assets/victorinox-kitchen.webp"
import go from "../assets/go.png";
import '../sections/style.css'

const Victorinox = () => {
    return (
        <Container>
            <Helmet>
                <title>   Victorinox   - Aksessuary.KZ</title>
                <meta name="description" content="    Victorinox  "/>
                <meta name="keywords" content="    Victorinox  ,    Victorinox  ,    Victorinox  , 
                   Victorinox  ,    Victorinox  ,   Victorinox, Victorinox,  Victorinox  " />
            </Helmet>
            <Card>
                <img src={ victorinox } alt="   Victorinox  "  width='30%' style={{display: 'block', marginLeft: 'auto', marginRight: 'auto'}}/>
                <p style={{textAlign: 'justify', marginLeft: '20px', marginRight: '20px'}}>
                     Victorinox           .    
                      ,     , ,    .      
                          ( ).               
                     1884 ,          .
                </p>
                <h4 style={{textAlign: 'center'}}>            !</h4>
                <Row style={{marginLeft: '10px', marginRight: '10px', marginBottom: '20px', marginTop: '10px'}}>
                    <Col md={8}>
                        <img src={ army } alt="    Victorinox  " width='100%'/>
                    </Col>
                    <Col md={4}>
                        <img src={ kitchen } alt="    Victorinox  " width='100%'/>
                    </Col>
                </Row>
                <a href='/shop'><img class='scalecat' src={ go } alt="   Victorinox  " style={{width: '20%', display: 'block', marginLeft: 'auto', marginRight: 'auto', marginBottom: '20px' }} /></a>
            </Card>
        </Container>
    )
}

export default Victorinox