import React, { useState } from "react";
import Menu from "./Menu";

const MobileMenu = () => {
    const [menuActive, setMenuActive] = useState(false)
    const items = [{value:'', href:'/'}, {value:' ', href:'/shop/?gender=3'}, {value:' ', href:'/shop/?gender=2'}, {value:' ', href:'/shop/?gender=2'}, {value:' Victorinox', href:'/shop/?gender=2'}, {value:' ', href:'/about'}, {value:'  ', href:'/delivery'}, {value:' ', href:'/service'}]

    return (
        <div className="show-on-mobile">
            <nav>
                <div className="burger-btn" onClick={() => setMenuActive(!menuActive)}>
                    <span />
                </div>
            </nav>
            <Menu active={menuActive} setActive={setMenuActive} header={""} items={items} />
        </div>
    )
}

export default MobileMenu