import { Container, Button } from 'react-bootstrap'
import { useContext } from 'react'
import { AppContext } from '../components/AppContext.js'
import { Link, useNavigate } from 'react-router-dom'
import { logout } from '../http/userAPI.js'
import { Helmet } from "react-helmet"

const Admin = () => {
    const { user } = useContext(AppContext)
    const navigate = useNavigate()

    const handleLogout = (event) => {
        logout()
        user.logout()
        navigate('/login', {replace: true})
    }

    return (
        <Container>
            <Helmet>
                <title>  - Aksessuary.KZ</title>
                <meta name="description" content="  "/>
            </Helmet>
            <h1> </h1>
            <p>
                     
            </p>
            <ul>
                <li><Link to="/admin/orders">  </Link></li>
                <li><Link to="/admin/brands"> </Link></li>
                <li><Link to="/admin/mehanizms"> </Link></li>
                <li><Link to="/admin/genders"></Link></li>
                <li><Link to="/admin/shapes"> </Link></li>
                <li><Link to="/admin/materials"> </Link></li>
                <li><Link to="/admin/glasses"></Link></li>
                <li><Link to="/admin/straps"> /</Link></li>
                <li><Link to="/admin/powers"> </Link></li>
                <li><Link to="/admin/waters"></Link></li>
                <li><Link to="/admin/catalogs"> </Link></li>
            </ul>
            <Button onClick={handleLogout}></Button>
        </Container>
    )
}

export default Admin