import { Card, Col, Row } from 'react-bootstrap'
import Container from 'react-bootstrap/Container'
import { Helmet } from 'react-helmet'
import waterman from "../assets/waterman-logo.png"
import watermanpen from "../assets/waterman1.webp"
import watermanp from "../assets/waterman2.webp"
import go from "../assets/go.png";

const Waterman = () => {
    return (
        <Container>
            <Helmet>
                <title>   Waterman   - Aksessuary.KZ</title>
                <meta name="description" content="    Waterman  "/>
                <meta name="keywords" content="  Waterman  ,    Waterman  ,    Waterman  , 
                      Waterman, Waterman,  Waterman  " />
            </Helmet>
            <Card>
                <img src={ waterman } alt="   Waterman  "  width='35%' style={{display: 'block', marginLeft: 'auto', marginRight: 'auto', marginTop: 15}}/>
                <p style={{textAlign: 'justify', marginLeft: '20px', marginRight: '20px'}}>
                       ,   Waterman     Waterman,  ,      1883.  
                     Waterman      .      CF,     1953.  
                     ,    ,    .         
                     ,        .
                </p>
                <p style={{textAlign: 'justify', marginLeft: '20px', marginRight: '20px'}}>
                    Waterman Paris   ,      -, , ,    . 
                              .    Waterman    
                     .  ,          .      
                       .
                </p>
                <h4 style={{textAlign: 'center', color: 'red'}}>            !</h4>
                <Row style={{marginLeft: '10px', marginRight: '10px', marginBottom: '20px', marginTop: '10px'}}>
                    <Col md={8}>
                        <img src={ watermanpen } alt="  Waterman  " width='100%'/>
                    </Col>
                    <Col md={4}>
                        <img src={ watermanp } alt="   Waterman  " width='90%'/>
                    </Col>
                </Row>
                <a href='/shop'><img class='scalecat' src={ go } alt="   Waterman  " style={{width: '20%', display: 'block', marginLeft: 'auto', marginRight: 'auto', marginBottom: '20px' }} /></a>
            </Card>
        </Container>
    )
}

export default Waterman