import { Card, Col, Row } from 'react-bootstrap'
import Container from 'react-bootstrap/Container'
import { Helmet } from 'react-helmet'
import swatch from "../assets/swatch-logo.jpg"
import swatchwatch from "../assets/swatch1.jpg"
import swatchw from "../assets/swatch2.jpg"
import go from "../assets/go.png";

const Swatch = () => {
    return (
        <Container>
            <Helmet>
                <title>  Swatch   - Aksessuary.KZ</title>
                <meta name="description" content="    Swatch  "/>
                <meta name="keywords" content="  Swatch  ,    Swatch  ,   Swatch  , 
                      Swatch, Swatch,  Swatch  " />
            </Helmet>
            <Card>
                <img src={ swatch } alt="   Swatch  "  width='40%' style={{display: 'block', marginLeft: 'auto', marginRight: 'auto'}}/>
                <p style={{textAlign: 'justify', marginLeft: '20px', marginRight: '20px'}}>
                         !     Swatch. ,        ,  
                         .
                </p>
                <p style={{textAlign: 'justify', marginLeft: '20px', marginRight: '20px'}}>
                            1983 .        ,    .    
                       .         .  Swatch  -: Swiss Watch  
                       Second Watch  , .     .         .
                         ,   Swatch    ,     ,     ,    
                     10 .         ,           Fashion,    .
                </p>
                <h4 style={{textAlign: 'center', color: 'red'}}>            !</h4>
                <Row style={{marginLeft: '10px', marginRight: '10px', marginBottom: '20px', marginTop: '10px'}}>
                    <Col md={8}>
                        <img src={ swatchwatch } alt="  Swatch  " width='92%'/>
                    </Col>
                    <Col md={4}>
                        <img src={ swatchw } alt="    Swatch  " width='100%'/>
                    </Col>
                </Row>
                <a href='/shop?brand=11'><img class='scalecat' src={ go } alt="   Swatch  " style={{width: '20%', display: 'block', marginLeft: 'auto', marginRight: 'auto', marginBottom: '20px' }} /></a>
            </Card>
        </Container>
    )
}

export default Swatch